.speakers {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  &__heading {
    @include heading;
  }

  &__speaker {
    width: 90%;
    // height: 300px;
    margin: 2rem auto 5rem auto;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    &__coming-soon{
      height: 100%;
      width: 100%;
      background: rgba(255, 255, 255,0.4);
      backdrop-filter: blur(6px);
      color: #222222;
      display:grid;
      place-items: center;
      position:absolute;
      top: -2rem;
      left: 0;
      z-index: 2;
      font-size: 3rem;
      border-radius: 8px;
      padding: 4rem 0;
      @include mobile{
        font-size: 2rem;
      }
    }
    .slide__wrapper {
      display: flex;
      justify-content: center;
      align-items: center;

      @include tablet {
        flex-direction: column;
      }
    }

    &__left {
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;

      @include tablet {
        width: 80%;
        margin-bottom: 2rem;
      }

      img {
        width: 60%;
        border-radius: 50%;
      }
    }
    &__right {
      flex: 1;
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: left;

      @include tablet {
        align-items: center;
        width: 80%;
        margin: auto;
      }

      .heading {
        font-size: 36px;
        color: $accent-color;
        margin-bottom: 3rem;
        font-weight: 600;
        position: relative;
        width: 80%;

        @include mobile {
          font-size: 24px;
        }

        &::before {
          content: "Who is in the House";
          position: absolute;
          font-size: 16px;
          top: -25px;
        }
      }
      .content {
        font-size: 18px;
        color: $accent-color;
        line-height: 27px;
        width: 80%;

        @include mobile {
          font-size: 14px;
        }
      }
      .line {
        margin-top: 1rem;
        background-color: $secondary-color;
        height: 5px;
        border-radius: 20px;
        width: 20%;
      }
    }
  }
  &__brochure {
    width: 100%;
    height: 300px;
    position: relative;
    margin: 6rem 0;
    margin-right: 0;
    position: relative;
    background-image: url("./../assets/brochureContainer.png");
    background-position: right;
    background-size: contain;
    background-repeat: no-repeat;
    @include mobile {
      background-size: cover;
      background-position: 80%;
    }
    &__content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: end;
      width: 80%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 15%;

      @include mobile {
        align-items: flex-end;
        right: 5%;
      }

      color: $accent-color;

      p {
        font-weight: 500;
        margin: 10px 0;
        font-size: 20px;
        text-align:right;

        @include mobile {
          font-size: 18px;
        }
      }
      :nth-child(2) {
        font-size: 14px;
        font-weight: 300;
        text-align:right;
      }
      .button {
        text-decoration: none;
        color: #ffffff;
        margin-top: 1.6rem;
        border: 2px solid $secondary-color;
        padding: 1rem 2rem;
        cursor: pointer;
        font-size: 18px;
        position: relative;
        z-index: 2;

        @include mobile {
          font-size: 18px;
        }
      }
    }
  }
}

.Ellipse_3202 {
  stroke: #ffffff;
  animation: rotate 8s ease-in-out infinite;
  transform-origin: 95.9558px 99.9998px;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
    stroke-opacity: 1;
  }
  80% {
    transform: rotate(360deg);
    stroke-opacity: 0.4;
  }
  100% {
    transform: rotate(0deg);
    stroke-opacity: 1;
  }
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-button-next {
  color: $secondary-color;
}
.swiper-button-prev {
  color: $secondary-color;
}
.pagination {
  display: none;
}
